import styles from './Cards.module.css'
import React from 'react';
import {Card, CardContent, Typography, Grid} from '@material-ui/core';
import CountUp from 'react-countup'
import cx from 'classnames';
import Spinner from '../UI/Spinner';


const Cards = (props) => {
  const {data : {confirmed, recovered, deaths, lastUpdate}} = props;
  if(!confirmed){
      return <Spinner/>;
  }
  return (
    <div className={styles.container}>
      <Grid container spacing={3} justify="center">

        <Grid item component={Card} xs={10} md={3} className={cx(styles.card, styles.infected)}>
            <CardContent>
                <Typography color="textSecondary" gutterBottom>Infected</Typography>
                <Typography variant="h5"><CountUp start={0} end={confirmed.value} duration={2.0} separator={','}></CountUp></Typography>
                <Typography color="textSecondary">{new Date(lastUpdate).toDateString()}</Typography>
                <Typography variant="body2" gutterBottom>Number of active cases of COVID-19</Typography>
            </CardContent>
        </Grid>

        <Grid item component={Card} xs={10} md={3} className={cx(styles.card, styles.recovered)}>
            <CardContent>
                <Typography color="textSecondary" gutterBottom>Recovered</Typography>
                <Typography variant="h5"><CountUp start={0} end={recovered.value} duration={2.0} separator={','}></CountUp></Typography>
                <Typography color="textSecondary">Real Date</Typography>
                <Typography variant="body2" gutterBottom>Number of recoveries from COVID-19</Typography>
            </CardContent>
        </Grid>

        <Grid item component={Card} xs={10} md={3} className={cx(styles.card, styles.deaths)}>
            <CardContent>
                <Typography color="textSecondary" gutterBottom>Deaths</Typography>
                <Typography variant="h5"><CountUp start={0} end={deaths.value} duration={2.0} separator={','}></CountUp></Typography>
                <Typography color="textSecondary">Real Date</Typography>
                <Typography variant="body2" gutterBottom>Number of deaths caused by COVID-19</Typography>
            </CardContent>
        </Grid>
      </Grid>
    </div>
  );
}

export default Cards;